<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process200"></a>

<p><br></p>
<p><span class="rvts17">英雄安息（</span><span class="rvts50">Heroes</span><span class="rvts17">’</span><span class="rvts50"> Rest</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span><span class="rvts17">：</span></p>
<p><span class="rvts41">坐标：</span>
<br><span class="rvts20">X:593,Y:139</span><span class="rvts21">，</span><span class="rvts20">NPC</span><span class="rvts21">：英雄石棺（</span><span class="rvts20">Heroes' Coffins</span><span class="rvts21">）</span>
<br><span class="rvts39"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">战胜人类，矮人，精灵，蜥蜴种族的英雄亡灵，并获得他们的宝物。</span>
<br><span class="rvts39"></span>
<br><span class="rvts41">任务详情：</span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1242.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts21">四个种族的英雄石棺位于石墓花园里面的一个小院子里，与石棺互动，会得知他们生前宝藏的埋藏地，全部发现这些宝箱需要至少</span><span class="rvts20">18</span><span class="rvts21">点的智慧值（</span><span class="rvts20">Wit</span><span class="rvts21">）。</span>
<br><span class="rvts21">然而，当与他们</span><span class="rvts20">4</span><span class="rvts21">个种族的所有石棺都发生了互动，那么</span><span class="rvts20">4</span><span class="rvts21">个种族的英雄将会与我们发生战斗。这些英雄需要杀两次，并且第二次会变得更加强大。</span>
<br><span class="rvts39"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1243.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts21">人类英雄宝箱位置位于</span><span class="rvts20">X:650,Y:200</span><span class="rvts21">。</span>
<br><span class="rvts39"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1244.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts21">矮人英雄宝箱位置位于</span><span class="rvts20">X:456,Y:394</span><span class="rvts21">。</span>
<br><span class="rvts39"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1245.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts21">精灵英雄宝箱位置位于</span><span class="rvts20">X:564,Y:461</span><span class="rvts21">。</span>
<br><span class="rvts39"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1246.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts21">蜥蜴英雄宝箱位置位于</span><span class="rvts20">X:732,Y:53</span><span class="rvts21">。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return {};
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



